.App {
  font-family: sans-serif;
  text-align: center;
}
.portfolioContainer {
  margin-bottom: 60px;
  /* background-color: #0099ff; */
}
.owl-item .item {
  min-height: 500px;
  display: flex;
  align-items: center;
}
.PortfolioheadingContainer .serviceDescription {
  margin-top: 10px;
}
.PortfolioheadingContainer {
  margin-bottom: 50px;
}
.portfolio-carousel .owl-dots {
  display: none;
}
.portfolio-carousel .owl-stage-outer {
  /* overflow-y:visible; */
}

.portfolio-carousel .owl-stage-outer {
  position: relative;
}
.portfolio-carousel .owl-nav {
  background-color: #fff;
  font-size: 40px;
  position: absolute;
  border-radius: 0px 15px 15px 0px;
  box-shadow: 0px 5px 15px #00000029;
  min-width: 50px;
  min-height: 60px;
  left: 50%;
  bottom: 50%;
}
.portfolio-carousel .owl-nav:hover {
  background-color: #2498fb;
}
.portfolio-carousel .owl-nav .owl-prev {
  display: none;
}
.carouselitem {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding-left: 40px;
  background-color: #0099ff;
  padding-right: 40px;
}
.carouselitem .carouselText .serviceDetailContainer {
  height: 110%;
  position: relative;
  top: -20px;
}
.carouselitem .carouselText .serviceDetailContainer .serviceDetails {
  height: 100%;
}
.carouselImage {
  width: 45%;
  align-self: center;
}
.carouselText {
  width: 45%;
}
.carouselText .serviceDetails {
  width: 90%;
}
.owl-theme .owl-nav [class*="owl-"]:hover {
  background-color: #2498fb;
}
.owlstageouter {
  overflow: visible;
}

@media only screen and (max-width: 897px) {
  .portfolio-carousel .owl-nav {
    left: 90%;
  }
  .carouselitem {
    align-items: center;
    flex-direction: column;
    margin: auto;
  }
  .carouselImage {
    width: 80%;
    margin-bottom: 30px;
  }
  .carouselText {
    width: 80%;
  }
  .portfolioContainer {
    margin-bottom: 20px;
    padding-top: 20px;
  }
}

