.testimonial-carousel {
  display: flex;
  justify-content: center;
  align-items: center;
}
.testimonial-carousel .owl-carousel {
  max-width: 500px;
}

.owl-dots {
  display: none;
}
.owl-carousel .owl-nav button.owl-prev {
  display: none;
}

.testimonial-carousel .owl-carousel .owl-nav button.owl-next {
  position: absolute;
  top: 50%;
  right: 10px;
  font-size: 50px;
}
