body {
  padding: 0;
  margin: 0;
  font-family: Open Sans, sans-serif;
  font-size: 16px;
  box-sizing: border-box;
  color: #646464;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Poppins;
  font-weight: 700;
}
h1,
h2 {
  font-weight: 700;
}
h3,
h4,
h5,
h6 {
  font-weight: 600;
}
p {
  line-height: 1.8;
  font-size: 16px;
  color: #646464;
  margin-bottom: 15px;
}

.d-table {
  width: 100%;
  height: 100%;
}
.d-table-cell {
  vertical-align: middle;
}
img {
  max-width: 100%;
}
button {
  outline: 0 !important;
}
.border {
  border-color: #eee !important;
}

a,
a:focus,
a:hover {
  text-decoration: none;
}
a {
  display: inline-block;
  transition: all 0.6s ease;
}

.MuiDrawer-paper {
  color: #fff !important;
  background-color: #2a2a2a !important ;
}

p.MuiTypography-body2.makeStyles-specialText-75:before {
}

main{
  padding-right: 60px;
  padding-left: 60px;
}

@media only screen and (max-width: 812px) {
main{
  padding-right: 20px;
  padding-left: 20px;
}
}

@media only screen and (min-width: 812px) and (max-width: 1140px) {
main{
  padding-right: 40px;
  padding-left: 40px;
}
}