.marketingContainer {
  background-color: #535772;
  min-height: 400px;
  height: max-content;
  flex-direction: column;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 50px;
  padding-bottom: 50px;
  align-items: center;
}
.imageContainer {
  width: 80%;
  align-items: center;
}

.imageContainer,
.marketingContainer,
.buttonContainer {
  display: flex;
  justify-content: center;
}

.image {
  max-height: 118px;
}

.marketingContainer .imageContainer .divider {
  background-color: #fff;
  width: 2px;
  border-radius: 12px;
  margin-left: 30px;
  margin-right: 30px;
  height: 80px;
  align-self: center;
}
.marketingContainer .imageContainer .cellarTechDesc {
padding-left: 0;
}
.marketingContainer h1 {
  font-size: 3em;
  font-weight: semi-bold;
  margin-bottom: 10;
}

.marketingContainer h4 {
  font-size: 1.5em;
  margin-top: 10px;
}

.marketingContainer h4,
.marketingContainer h1 {
  text-align: center;
  color: #fff;
  font-family: Poppins;
}

.buttonContainer {
  margin-top: 40px;
  padding-left: 20px;
  padding-right: 20px;
}

@media only screen and (max-width: 767px) {
  .imageContainer,
  .marketingContainer,
  .buttonContainer {
    flex-direction: column;
    align-items: center;
  }
  .marketingContainer {
    min-height: 500px;
  }
  .marketingContainer .imageContainer .divider{
    display: none;
  }
  .image {
    max-height: 80px;
  }
  .marketingContainer h1 {
    font-size: 2em;
  }
  .marketingContainer h4 {
    font-size: 1.3em;
  }

  .buttonContainer button:nth-child(1) {
    margin-bottom: 20px;
    margin-right: 0;
  }
}
